<template>
  <div class="detail-wrapper">
    <!-- 详情图 -->
    <div class="header-img" >
      <!-- 轮播图 -->
      <page-jump></page-jump>
      <swiper style="height: 320px" ref="mySwiper" :options="swiperOptions">
        <swiper-slide v-for="(item,index) in ImagesIsEmpty" :key="index">
          <img :src="item">
        </swiper-slide>
        <div v-show="ImagesIsEmpty.length > 1" class="swiper-pagination" slot="pagination"></div>
      </swiper> 
    </div>
    <!-- 红色背景 -->
    <div class="bg-red">
      <div class="right">
        <div class="fz-20 w-100">曾苗苗老师的教师节倒计时：</div>
        <div class="count-box fz-20">
          <ul v-if="end > now">
            <li>{{d}}</li>
            <li class="date">天</li>
            <li>{{h}}</li>
            <li class="date">时</li>
            <li>{{m}}</li>
            <li class="date">分</li>
            <li>{{s}}</li>
            <li class="date">秒</li>
          </ul>
        </div>
      </div>
    </div>
    <!-- 内容区 -->
    <div class="content">
      <!-- 文字简介 -->
      <div class="brief-box">
        <!-- 热卖 -->
        <div class="hot-sell">
          <div class="tag-box">
            <div class="left" v-for="item in tags" :key="item.id">{{item.tagName}}</div>
            <!-- <div class="right">抖音热卖</div> -->
          </div>
          <div class="cor-9c9">{{detailObj.spreadNum}}人热推中</div>
        </div>
        <!-- 折扣 -->
        <div class="hot-discount">
          <p>{{detailObj.skuName}}</p>
          <div class="price-discount">{{detailObj.disCount}}人</div>
        </div>
        <!-- 推荐理由-->
        <div class="recommend-reason">
          推荐理由: 教师节
        </div>
        <!-- 月销，库存 -->
        <div class="stock-box">
          <div class="ml-12 cor-9c9">月推送<b>{{detailObj.monthSales}}</b></div>
          <div class="ml-12 fz-20 d-flex a-center cor-ff2342">红包<b class="ml-12">20000</b></div>
        </div>
      </div>
      <!-- 商品详情图 -->
      <div class="detail-box">
        <div class="title">图片详情</div>
        <div class="detail-image" v-for="(v,i) in imgList" :key="i">
          <img :src="v">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 引入接口地址
export default {
  name:'Detail',
  props: {},
  data() {
    return {
      // 详情对象
      detailObj:{
        ratePrice: 20,
        price: 30,
        skuNum: 20,
        spreadNum: '20',
          skuName: '参与人数',
          disCount: 500,
          reason: '结婚喜庆',
          monthSales:'100'
      },
      // 标签
      tags:[
        {
          id:1,
          tagName: '抖音热推',
          spreadNum: '20',
          skuName: '折扣',
          disCount: 20,
          reason: '不错',
          monthSales:'100'
        },
      ],
      // 天
      d: 0,
      // 小时
      h: 0,
      // 分
      m: 0,
      // 秒
      s: 0,
      // 结束时间
      end: 0,
      // 现在时间
      now: 0,
      // 定时器
      timer:0,
      // 图片数组
      skuImages: [],
      swiperOptions: {
        pagination: {
          el: '.swiper-pagination',
          // clickable设置为true时，点击分页器的指示点分页器会控制Swiper切换
          clickable :true
        },
        // Some Swiper option/callback...
        autoplay:{
          delay: 5000,
          disableOnInteraction:false
        },
        // loop:true, // 衔接顺畅的设置
      }
    }
  },
  computed: {
    //商品详情
    imgList() {
      return [
        require('../assets/images/1-teacher.jpg'),
        require('../assets/images/2-teacher.jpg'),
      ]
    },
    // 剔除空的图片
    ImagesIsEmpty() {
      return this.skuImages.filter(item => item != "");
    },
    // swiper
    swiper() {
      return this.$refs.mySwiper.$swiper
    }
  },
  methods: {
    // 返回首页
    toReturn() {
      this.$router.push('/')
    },
    // 倒计时
    countTime() {
      // 结束时间戳
      const end = this.end;
      //定义当前时间戳
      this.now = this.now + 1000; //后端给的定时器是死的，每次加一秒就可以自动变化了
      // 做判断当倒计时结束时都为0
      if (this.now >= end) {
        this.d = 0
        this.h = 0
        this.m = 0
        this.s = 0
        return
      }
      // 用结束时间减去当前时间获得倒计时时间戳
      const msec = end - this.now
      let d = parseInt(msec / 1000 / 60 / 60 / 24) //算出天数
      let h = parseInt(msec / 1000 / 60 / 60 % 24) //算出小时数
      let m = parseInt(msec / 1000 / 60 % 60) //算出分钟数
      let s = parseInt(msec / 1000 % 60) //算出秒数
      //给数据赋值
      this.d = d
      this.h = h > 9 ? h : '0' + h
      this.m = m > 9 ? m : '0' + m
      this.s = s > 9 ? s : '0' + s
    },
    goodsDetail() {
      this.end = new Date('2022-9-10')
      this.now = Date.now();
      // 轮播图片数组
      this.timer = setInterval(()=> {
        this.countTime();
      }, 1000)
    }
  },
  mounted() {
    // 调用商品详情接口
    this.goodsDetail()
    this.swiper.slideTo(5, 1000, false);
    this.skuImages = [this.$route.query.item.img]
    console.log("11111111",this.skuImages)
  },

  watch: {},
  beforeDestroy() {
    clearInterval(this.timer);
    this.timer = null;
  }
}
</script>

<style scoped>
  .detail-wrapper {
    min-height: 100vh;
    background-color: #f0f0f0;
  }
  /* 详情图 */
  .header-img{
