    width: 100%;
    height: 100%;
  }
  .swiper-slide img {
    width: 100%;
    height: 100%;
    display: inline-block;
  }
  /* 修改轮播indicator指示灯的选中状态 */
  /deep/ .swiper-pagination-bullet-active {
    background-color: #0096e6;
  }
  .return {
    background: rgba(0,0,0,0.3);
    position: fixed;
