    transform: scaleY(1.5) translate(21%, 10%);
  }
  /* 红色背景 */
  .bg-red {
    height: 140px;
    background-image:linear-gradient(to right,#ff2342,#ff2a4b,#ff446d);
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    white-space: nowrap;
  }
  .left {
    height: 80px;
  }
  .top {
    display: flex;
    height: 40px;
    color: #ffffff;
    align-items:flex-end;
  }
  .new-price {
    margin-right: 15px;
  }
