    font-size: 40px;
    font-weight: bold;
  }
  .factory-price {
    width: 1px;
    text-align: center;
    color: #f04f59;
    font-size: 12px;
    border-radius: 20px;
    background-color: #d3d3d3;
    padding:8px 10px 6px 10px;
    box-sizing: border-box;
