  }
  .tag-box .left:nth-of-type(even) {
    background-image: linear-gradient(90deg,#475dfc,#19a4fc);
  }
  /* .tag-box .right{
  } */
  /* 折扣 */
  .hot-discount {
    height:70px;
    line-height: 70px;
    display: flex;
    align-items: center;
  }
  .hot-discount p{
    font-size: 30px;
    margin-right: 20px;
    height: 70px;
    font-weight: 600;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
